<template>
  <div class="container">
    <div class="container" v-show="isFrist">
      <div class="first-page">
        <span class="text-gray"></span>
        <img src="../../../assets/shop-bind.png"/>
        <button class="btn custom-btn btn-block" @click="showPage"></button>
      </div>
    </div>
    <div class="container" v-show="!isFrist">
      <area-select :info="data.info" :list="data.list" :open="openSelelct" @selectChange="selectChange($event,data)"></area-select>
      <div class="shopLists" v-if="shopList.length">
        <div class="shop-panel" v-for = "item in shopList" :key="item.index" :class="{ isSelsected: item.selected }" @click="shopSelect(item)">
          <div class="title">
            <i class="iconfont icon-lottery-shop i-lightgreen f-18" :class="{ iGreen: item.selected }"></i>
            <span class="pl-5">{{item.view_name}}</span>
          </div>
          <div class="address">
            {{item.address}}
          </div>
          <div class="icon-position" v-if="item.selected"><i class="iconfont icon-selected iGreen f-18"></i></div>
        </div>
      </div>
      <div class="empty" v-if="!shopList.length">
        <img src="../../../assets/shop-empty.png"/>
        <span></span>
      </div>
      <footer class="mt-60" v-if="shopList.length">
        <button class="btn-self f-18"  @click="bindShop"></button>
      </footer>
    </div>
  </div>
</template>
<script>
import areaSelect from '@/shared/components/area-select'
import listSelect from '@/shared/components/list-select'
import { CITY_CODE } from '../../shared/area-code/pcas-code'
import { MessageBox } from 'mint-ui'
import {api} from '@/api'
export default {
  components: {
    listSelect,
    areaSelect
  },
  data () {
    return {
      data: {
        info: {
          lab: '',
          title: '',
          id: 'province'
        },
        list: CITY_CODE,
        selectItem: null
      },
      shopList: [

      ],
      shopId: '',
      machine_id: '',
      domHeight: '',
      isFrist: true,
      openSelelct: false
    }
  },
  created () {
    this.machine_id = this.$route.query.machine_id || ''
  },
  mounted () {
    // this.domHeight = this.$el.parentElement.clientHeight - 120
  },
  methods: {
    showPage () {
      this.openSelelct = true
    },
    selectChange (e, item) {
      console.log(e, item)
      if (item.info.id === 'province') {
        let code = e[e.length - 1].id
        this.getShopList(code)
      }

      this.isFrist = false
    },
    getShopList (code) {
      this.shopList = []
      api.requestWithSid('hunan/passimeter/Machine/clubList', {area_code: code})
        .then(res => {
          if (res.data.length) {
            for (let item of res.data) {
              item['selected'] = false
            }
            this.shopList = res.data
          }
        })
        .catch(err => {
          MessageBox.alert(err.msg)
          console.log(err)
        })
    },
    shopSelect (item) {
      for (let shop of this.shopList) {
        shop.selected = false
      }

      item.selected = true
      this.shopId = item.id
    },
    bindShop () {
      if (this.shopId !== 'null' && this.machine_id) {
        let reaData = {
          club_id: this.shopId,
          machine_id: this.machine_id
        }
        api.requestWithSid('hunan/passimeter/Machine/bindMachine', reaData)
          .then(res => {
            MessageBox.alert('').then(action => {
              this.$router.push({path: '/add-lottery', query: {machine_id: this.machine_id}})
            })
          })
          .catch(err => {
            MessageBox.alert(err.msg)
            console.log(err)
          })
      }
    }
  }
}
</script>
<style scoped>
  .container{
    height: 100%;
    background: #EFF0F4;
    overflow: hidden;
  }
  .shopLists{
    overflow: auto;
    height: 100%;
    box-sizing: border-box;
    padding-bottom: 120px;
  }
  footer{
    width: 100%;
    position: fixed;
    bottom: 0;
  }
.btn-self {
  width: 100%;
  height: 55px;
  background: #006d55;
  color: #fff;
  border: 0;
  outline: none;
  -moz-box-shadow:2px 2px 15px #AEB6B3;
  -webkit-box-shadow:2px 2px 15px #AEB6B3;
  box-shadow:2px 2px 15px #AEB6B3;
}
.btn-self:active {
  background: #005b48 ;
}

.first-page{
  text-align: center;
  width: 70%;
  margin: auto;
  padding-top:60px;
}
.first-page img {
  width: 70%;
  margin: 90px auto 100px;
}
.first-page button{
  width: 100%
}
.shop-panel{
  background: #fff;
  margin: 5px 10px;
  padding: 20px;
  border-radius: 5px;
  position: relative;
  transition:all 0.5s;
}
.shop-panel .address {
  margin-left: 20px;
  margin-top: 5px;
  font-size: 14px;
  color: #ABABAB;
}
.pl-5{
  padding-left: 5px;
}
.isSelsected{
  box-shadow: 0px 2px 10px #bdbbbb;
}
.empty{
  text-align: center;
  width: 55%;
  margin: auto;
  margin-top: 90px;
}
.empty img{
  width: 100%;
}
.empty span{
  color:#a2a2a2;
}

.icon-position{
  position: absolute;
  right: 16px;
  top: 32px;
}
.iGreen{
  color: #106D56 !important;
  transition:all 0.5s;
}
.bg-white{
  background: #fff;
}

</style>



