<template>
  <div class="container">
    <div class="container-box">
      <div>
        <img class="w-100" src="../../../assets/add-success.png" alt="">
      </div>
      <div>
        <div class="text"></div>
        <div><span class="i-red">{{num}}</span></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'add-success',
  data: function () {
    return {
      num: 0
    }
  },
  created () {
    this.num = this.$route.query.num || 0
  },
  methods: {}
}
</script>

<style scoped>
  .container{
    height: 100%;
    box-sizing: border-box;
    background: url('../../../assets/login-bg.png') no-repeat;
    background-size: 100% 100%;
    background-attachment: fixed;
    color: #434345;
  }
  .container-box{
    width: 60%;
    margin: auto;
    padding-top: 120px;
    text-align: center;
    font-size: 15px;
    color: rgba(0,0,0,0.4);
    letter-spacing: 1px;
  }
  .w-100{
    width: 100%;
  }
  .text{
        padding: 20px 0 5px;
  }
</style>



