<template>
  <div class="content">
    <div v-if="isInUse">
      <img src="../../../assets/machine-in-use.png">
      <div class="f-16 title">{{msg}}</div>
    </div>
    <div v-else>
      <img src="../../../assets/machine-lock.png">
      <div class="f-16"></div>
      <div class="f-14 info">{{msg}}</div>
    </div>
    <div class="btn-container" v-if="canToOrder">
      <a class="btn custom-btn-outline" @click="toOrder"></a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MachineLocked',
  created () {
    this.$store.dispatch('getLockedMsgMap')
    this.$store.dispatch('setMsgCode')
  },
  computed: {
    msg () {
      return this.$store.getters.lockedMsg
    },
    isInUse () {
      return Number(this.$store.state.machineInfo.msgCode) === 12
    },
    canToOrder () {
      // msgCode 1: machine qr code is illegal
      return Number(this.$store.state.machineInfo.msgCode) !== 1
    }
  },
  methods: {
    toOrder () {
      this.$router.push('orders')
    }
  }
}
</script>

<style scoped>
.content {
  height: 100%;
  text-align: center;
  letter-spacing: 1px;
  color: rgba(0, 0, 0, 0.7);
}
img {
  margin-top: 20%;
  margin-bottom: 20px;
}
.info {
  color: rgba(0, 0, 0, 0.5);
  margin-top: 10px;
}
.btn-container {
  margin-top: 20%;
}
</style>



