<template>
  <div id="app" :style = "{ height:pageHeight + 'px' }">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      pageHeight: ''
    }
  },
  mounted () {
    this.pageHeight = this.$root.$el.parentElement.parentElement.clientHeight
    window.addEventListener('resize', this.resizeEventHandle)
  },
  methods: {
    resizeEventHandle () {
      this.pageHeight = this.$root.$el.parentElement.parentElement.clientHeight
    }
  },
  destroyed () {
    window.removeEventListener('resize', this.resizeEventHandle)
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  /* color: #2c3e50;
  margin-top: 60px; */
}
body{
  padding: 0 !important;
  margin: 0 !important;
}
</style>



