import axios from 'axios'
import qs from 'qs'
import store from '../store'
let BASE_URL = process.env.NODE_ENV === 'development' ? 'http://test.yan.eeseetech.cn' : ''
BASE_URL += '/$SERVICE'

export default {
  request (service, data) {
    let url = BASE_URL
    url = url.replace('$SERVICE', service)
    data = (data === undefined) ? {} : data
    data['machine_id'] = store.state.session.machineId
    return axios.post(url, qs.stringify(data), {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    }).then(res => {
      if (res.status === 200 && res.data && !res.data.code) {
        return res.data
      } else {
        return Promise.reject(res.data)
      }
    })
  },
  requestWithOpenid (service, data) {
    data = (data === undefined) ? {} : data
    data['openid'] = store.state.session.openid
    return this.request(service, data)
  },
  requestWithSid (service, data) {
    data = (data === undefined) ? {} : data
    data['sid'] = store.state.session.sid
    return this.request(service, data)
  },
  getUrlParamsBy (name) {
    var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)')
    var r = window.location.hash.split('?')[1]
    if (r !== undefined) {
      r = r.match(reg)
      if (r != null) return unescape(r[2])
    }
    return null
  },
  navigateWithMachineId (service, data) {
    let url = BASE_URL
    url = url.replace('$SERVICE', service)
    data = data === undefined ? {} : data
    data['machine_id'] = store.state.session.machineId
    let param = '?'
    for (let key in data) {
      param += `${key}=${data[key]}&`
    }
    url += param
    window.location.assign(url)
  },
  get (service) {
    let url = BASE_URL
    url = url.replace('$SERVICE', service)
    return axios.get(url).then(res => {
      if (res.status === 200) {
        return res.data
      } else {
        return Promise.reject(res.data)
      }
    })
  }
}



