<template>
  <div class="form-lists " @click="selectObj.show()">
    <div class="lab">
      <i class="iconfont" :class="info.icon"></i>
    </div>
    <div class="value selec-box ml-10" :id="info.id">
      <p>{{info.title}}</p>
    </div>
    <div class="ml-10">
      <svg style="height:20px;fill:#106D56" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512"><path d="M31.3 192h257.3c17.8 0 26.7 21.5 14.1 34.1L174.1 354.8c-7.8 7.8-20.5 7.8-28.3 0L17.2 226.1C4.6 213.5 13.5 192 31.3 192z"/>
      </svg>
    </div>
  </div>
</template>

<script>
import MobileSelect from 'mobile-select'
import {SelectInit} from '../area-code/select-init'
export default {
  name: 'list-select',
  props: ['list', 'info', 'init'],
  data: function () {
    return {
      selectObj: null,
      selected: null,
      isInit: false
    }
  },
  watch: {
    init: function (n, o) {
      if (n) {
        this.initDataFn(n.id, n.code)
      } else {
        this.isInit = false
      }
    },
    list: function (n, o) {
      this.selectObj.updateWheel(0, n)
      if (!this.isInit) {
        this.selectObj.locatePosition(0, 0)
        this.$el.querySelector('.selec-box').innerHTML = `<p>${this.info.title}</p>`
      }
    }
  },
  methods: {
    initDataFn: function (id, code) {
      if (!id) {
        return
      }
      this.isInit = true
      let initData = new SelectInit(id, code)
      initData.startCount(this.list)
      for (var i = 0; i < initData.areaIndex.length; i++) {
        this.selectObj.locatePosition(i, initData.areaIndex[i])
      }
      this.$el.querySelector('.selec-box').innerHTML = `${initData.value}`
      this.$emit('selectChange', { data: initData.data, isInit: this.isInit })
    }
  },
  mounted: function () {
    var _vue = this
    this.selectObj = new MobileSelect({
      trigger: '#' + this.info.id,
      title: this.info.title,
      wheels: [{
        data: this.list
      }],
      position: [0],
      callback: function (indexArr, data) {
        _vue.selected = data
        _vue.isInit = false
        _vue.$emit('selectChange', { data: _vue.selected, isInit: _vue.isInit })
      }
    })
  },
  destroyed: function () {
    let selectEles = document.querySelectorAll('.mobileSelect')
    let body = document.querySelector('body')
    for (let item of selectEles) {
      body.removeChild(item)
    }
  }
}
</script>
<style scoped>
.form-lists  {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 60px;
  width: 80%;
  margin: auto;
  background: #fff;
}

.form-lists  .lab {
  color: rgba(0, 0, 0, 0.65);
  font-weight: 500;
  padding-top: 1px;
  font-size: 15px;
}
.form-lists  .value {
  font-size: 16px;
  min-width: 100px;
}

.ml-10{
  margin-left: 10px;
}
</style>



