<template>
  <div class="form-lists" @click="selectObj.show()">
    <div class="lab">
      <i class="iconfont icon-address i-green"></i>
    </div>
    <div class="value selec-box ml-10" :id="info.id">
      <p>{{info.title}}</p>
    </div>
    <div class="ml-10">
      <i class="iconfont icon-drop-dowm i-green f-12"></i>
    </div>
  </div>
</template>

<script>
import MobileSelect from 'mobile-select'
export default {
  name: 'area-select',
  props: ['list', 'info', 'init', 'open'],
  data: function () {
    return {
      selectObj: null,
      selected: null
    }
  },
  watch: {
    open: function (n, o) {
      if (n) {
        this.selectObj.show()
      }
    },
    init: function (n, o) {
    },
    list: function (n, o) {
      this.selectObj.updateWheel(0, n)
      this.selectObj.locatePosition(0, 0)
      this.$el.querySelector('.selec-box').innerHTML = `<p>${this.info.title}</p>`
    }
  },
  methods: {

  },
  mounted: function () {
    var _vue = this
    this.selectObj = new MobileSelect({
      trigger: '#' + this.info.id,
      title: this.info.title,
      wheels: [{
        data: this.list
      }],
      position: [0],
      callback: function (indexArr, data) {
        _vue.selected = data
        _vue.$emit('selectChange', _vue.selected)
      }
    })
  },
  destroyed: function () {
    let selectEles = document.querySelectorAll('.mobileSelect')
    let body = document.querySelector('body')
    for (let item of selectEles) {
      body.removeChild(item)
    }
  }
}
</script>
<style scoped>
.form-lists  {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  min-height: 50px;
  padding: 0 10px;
}

.form-lists .lab {
  color: rgba(0, 0, 0, 0.65);
  font-weight: 500;
  padding-top: 1px;
  font-size: 15px;
}
.form-lists  .value {
  font-size: 14px;
  margin-left: 10px;
  min-width: 100px;
}

.selec-box > p {
  color: rgba(0, 0, 0, 0.2);
}

.ml-10{
  margin-left: 10px;
}
</style>



