<template>
<div class="box">
  <div class="text">
    
  </div>
  <div class="err-info">
    <div class="err-text" v-if="isError">
      <span class="icon"> <svg xmlns="http://www.w3.org/2000/svg" style="height:13px;fill:#FE5252" viewBox="0 0 512 512"><path d="M504 256c0 136.997-111.043 248-248 248S8 392.997 8 256C8 119.083 119.043 8 256 8s248 111.083 248 248zm-248 50c-25.405 0-46 20.595-46 46s20.595 46 46 46 46-20.595 46-46-20.595-46-46-46zm-43.673-165.346l7.418 136c.347 6.364 5.609 11.346 11.982 11.346h48.546c6.373 0 11.635-4.982 11.982-11.346l7.418-136c.375-6.874-5.098-12.654-11.982-12.654h-63.383c-6.884 0-12.356 5.78-11.981 12.654z"/></svg></span>
      <span>{{errMsg}}</span>
    </div>
  </div>
  <div class="input-box">
    <div class="relative">
      <input :type="inputType" placeholder="" v-model="password" />
      <div class="show-pwd" @click="inputTypeChange">
        <svg v-if="iocnShow" xmlns="http://www.w3.org/2000/svg" style="height:20px;fill:rgba(0,0,0,0.6)" viewBox="0 0 576 512"><path d="M569.354 231.631C512.969 135.949 407.81 72 288 72 168.14 72 63.004 135.994 6.646 231.631a47.999 47.999 0 0 0 0 48.739C63.031 376.051 168.19 440 288 440c119.86 0 224.996-63.994 281.354-159.631a47.997 47.997 0 0 0 0-48.738zM288 392c-75.162 0-136-60.827-136-136 0-75.162 60.826-136 136-136 75.162 0 136 60.826 136 136 0 75.162-60.826 136-136 136zm104-136c0 57.438-46.562 104-104 104s-104-46.562-104-104c0-17.708 4.431-34.379 12.236-48.973l-.001.032c0 23.651 19.173 42.823 42.824 42.823s42.824-19.173 42.824-42.823c0-23.651-19.173-42.824-42.824-42.824l-.032.001C253.621 156.431 270.292 152 288 152c57.438 0 104 46.562 104 104z"/></svg>
        <svg v-if="!iocnShow" xmlns="http://www.w3.org/2000/svg" style="height:20px;fill:rgba(0,0,0,0.6)" viewBox="0 0 576 512"><path d="M286.693 391.984l32.579 46.542A333.958 333.958 0 0 1 288 440C168.19 440 63.031 376.051 6.646 280.369a47.999 47.999 0 0 1 0-48.739c24.023-40.766 56.913-75.775 96.024-102.537l57.077 81.539C154.736 224.82 152 240.087 152 256c0 74.736 60.135 135.282 134.693 135.984zm282.661-111.615c-31.667 53.737-78.747 97.46-135.175 125.475l.011.015 41.47 59.2c7.6 10.86 4.96 25.82-5.9 33.42l-13.11 9.18c-10.86 7.6-25.82 4.96-33.42-5.9L100.34 46.94c-7.6-10.86-4.96-25.82 5.9-33.42l13.11-9.18c10.86-7.6 25.82-4.96 33.42 5.9l51.038 72.617C230.68 75.776 258.905 72 288 72c119.81 0 224.969 63.949 281.354 159.631a48.002 48.002 0 0 1 0 48.738zM424 256c0-75.174-60.838-136-136-136-17.939 0-35.056 3.473-50.729 9.772l19.299 27.058c25.869-8.171 55.044-6.163 80.4 7.41h-.03c-23.65 0-42.82 19.17-42.82 42.82 0 23.626 19.147 42.82 42.82 42.82 23.65 0 42.82-19.17 42.82-42.82v-.03c18.462 34.49 16.312 77.914-8.25 110.95v.01l19.314 27.061C411.496 321.2 424 290.074 424 256zM262.014 356.727l-77.53-110.757c-5.014 52.387 29.314 98.354 77.53 110.757z"/></svg>
      </div>
    </div>
  </div>
  <div class="btn-box">
    <button @click="btnClicked"></button>
  </div>
</div>
</template>
<script>
import { api } from '@/api'
export default {
  name: 'loginVerify',
  data () {
    return {
      password: '',
      pageHeight: 0,
      isError: false,
      errMsg: '',
      machine_id: '',
      inputType: 'password',
      iocnShow: false
    }
  },
  created () {
    this.machine_id = this.$route.query.machine_id || ''
  },
  methods: {
    inputTypeChange () {
      this.iocnShow = !this.iocnShow
      this.inputType = this.inputType === 'password' ? 'text' : 'password'
    },
    btnClicked () {
      api
        .requestWithSid('hunan/passimeter/Machine/doLogin', {
          machine_id: this.machine_id,
          pwd: this.password
        })
        .then(res => {
          this.isError = false
          this.errMsg = ''
          if (res.data.status) {
            this.$router.push({path: '/bind-shop', query: {machine_id: this.machine_id}})
          } else {
            this.$router.push({path: '/add-lottery', query: {machine_id: this.machine_id}})
          }
        })
        .catch(err => {
          this.isError = true
          this.errMsg = err.msg
          console.log(err)
        })
    }
  }
}
</script>
<style scoped>
.box {
  height: 100%;
  background: url('../../../assets/login-bg.png') no-repeat;
   background-size: 100% 100%;
  background-attachment: fixed;
  color: #434345;
}
.box > .text {
  font-weight: 300;
  font-size: 30px;
  padding-top: 70px;
  margin-bottom: 50px;
  text-align: center
}
.err-info {
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
}
.err-info > .err-text {
  display: flex;
  align-items: center;
  font-size: 12px;
  width: 80%;

}
.err-info > .err-text > .icon {
  margin-right: 5px;
  padding-top: 4px;
}
.input-box {
  text-align: center;
}
.input-box input {
  width: 80%;
  height: 50px;
  border: 0;
  outline: none;
  padding-left: 15px;
  box-sizing: border-box;
  -moz-box-shadow:2px 2px 15px #DEDBDC;
  -webkit-box-shadow:2px 2px 15px #DEDBDC;
  box-shadow:2px 2px 15px #DEDBDC;
}
.relative{
  position: relative;
}
.show-pwd{
  position: absolute;
  right: 12%;
  top: 15px
}
.btn-box {
  text-align: center;
  padding-top: 40px;
}
.btn-box button {
  width: 80%;
  height: 55px;
  background: #006d55;
  color: #fff;
  border: 0;
  outline: none;
  font-size: 18px;
  -moz-box-shadow:2px 2px 15px #AEB6B3;
  -webkit-box-shadow:2px 2px 15px #AEB6B3;
  box-shadow:2px 2px 15px #AEB6B3;
}
.btn-box button:active {
  background: #005b48 ;
}
</style>



