import Vue from 'vue'
import Router from 'vue-router'
import loginVerify from '@/components/operator/login-verify'
import bindShop from '@/components/operator/bind-shop'
import addLottery from '@/components/operator/add-lottery'
import orders from '@/components/orders/Orders'
import payCancel from '@/components/orders/PayCancel'
import payFailure from '@/components/orders/PayFailure'
import paySuccess from '@/components/orders/PaySuccess'
import machineLocked from '@/components/orders/MachineLocked'
import addSuccess from '@/components/operator/add-success'

Vue.use(Router)

export const router = new Router({
  routes: [
    {
      path: '/login-verify',
      component: loginVerify,
      meta: {title: ''}
    },
    {
      path: '/bind-shop',
      component: bindShop,
      meta: {title: ''}
    },
    {
      path: '/add-lottery',
      component: addLottery,
      meta: {title: ''}
    },
    {
      path: '/add-success',
      component: addSuccess,
      meta: {title: ''}
    },
    {
      path: '/orders',
      component: orders
    },
    {
      path: '/payCancel',
      component: payCancel,
      meta: {title: ''}
    },
    {
      path: '/payFailure',
      component: payFailure,
      meta: {title: ''}
    },
    {
      path: '/paySuccess',
      component: paySuccess,
      meta: {title: ''}
    },
    {
      path: '/locked',
      component: machineLocked
    }
  ]
})
router.beforeEach((to, from, next) => {
  let sid = from.query.sid
  if (sid) {
    to.query['sid'] = sid
  }
  next()
})
router.afterEach((to, from) => {
  let titleEle = document.getElementsByTagName('title')[0]
  let title = (to.meta && to.meta.title) ? to.meta.title : ''
  titleEle.innerHTML = title
})



