// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import './vendors/font-css/iconfont.css'
import './vendors/custom-css/custom.css'
import Vue from 'vue'
import App from './App'
import { router } from './router'
import store from './store'

Vue.config.productionTip = false
Vue.use(MintUI)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>',
  created () {
    this.$store.dispatch('initSessionParam', 'sid')
    // this.$store.dispatch('initSessionParam', 'openid')
    this.$store.dispatch('initSessionParam', 'machine_id')
  }
})



