import { api } from '../../api'

// initial state
const state = {
  sid: '',
  openid: '',
  machineId: ''
}
// getters
const getters = {
}
// actions
const actions = {
  initSessionParam ({ dispatch }, type) {
    let param = api.getUrlParamsBy(type)
    if (!param) {
      dispatch('recover', type)
    } else {
      let data = {}
      data[type] = param
      dispatch('cacheSession', data)
    }
  },
  cacheSession ({ commit }, data) {
    if (data.sid) {
      commit('setSid', data.sid)
      localStorage.setItem('sid', data.sid)
    }
    if (data.openid) {
      commit('setOpenid', data.openid)
      localStorage.setItem('openid', data.openid)
    }
    if (data.machine_id) {
      commit('setMachineId', data.machine_id)
      localStorage.setItem('machine_id', data.machine_id)
    }
  },
  recover ({ commit }, type) {
    switch (type) {
      case 'sid':
        let localSid = localStorage.getItem('sid') || ''
        commit('setSid', localSid)
        break
      case 'openid':
        let localOpenid = localStorage.getItem('openid') || ''
        commit('setOpenid', localOpenid)
        break
      case 'machine_id':
        let localMachineId = localStorage.getItem('machine_id') || ''
        commit('setMachineId', localMachineId)
        break
    }
  }
}
// mutations
const mutations = {
  setSid (state, sid) {
    state.sid = sid
  },
  setOpenid (state, openid) {
    state.openid = openid
  },
  setMachineId (state, machineId) {
    state.machineId = machineId
  }
}
export default {
  state,
  getters,
  actions,
  mutations
}



