import { api } from '../../api'

let _timer = null

const state = {
  MIN_NUM: 1,
  MAX_NUM: 10,
  num: 1,
  orderId: '',
  orderDetail: {},
  polling: false,
  isTimeout: false,
  isError: false,
  errorMsg: '',
  submitting: false
}

const getters = {
  totalPrice: (state, rootState) => {
    return (state.num * rootState.price)
  },
  isSuccess: state => Number(state.orderDetail.ticket_status) === 1,
  apiState: state => {
    return {
      polling: state.polling,
      isError: state.isError,
      errorMsg: state.errorMsg
    }
  }
}

const actions = {
  setNumToTen ({ commit }) {
    commit('setNum', 10)
  },
  setNum ({ commit }, num) {
    if (Number(num)) {
      commit('setNum', Number(num))
    }
  },
  increase ({ commit }) {
    if (state.num < state.MAX_NUM) {
      commit('increase')
    }
  },
  decrease ({ commit }) {
    if (state.num > state.MIN_NUM) {
      commit('decrease')
    }
  },
  resetOrder ({ commit, state }) {
    commit('setNum', state.MIN_NUM)
    commit('setSubmitting', false)
  },
  submitOrder ({ state, getters, rootState, commit }) {
    if (state.submitting) {
      return
    }
    commit('setSubmitting', true)
    let param = {
      'ticket_id': rootState.machineInfo.lotteryId,
      'ticket_num': state.num,
      'total_fee': getters.totalPrice,
      't': new Date().getTime()
    }
    api
      .navigateWithMachineId(
        'hunan/passimeter/Consumer/submitOrder',
        param
      )
  },
  getOrderId ({ commit }) {
    let orderId = api.getUrlParamsBy('order_id') || ''
    commit('setOrderId', orderId)
  },
  resetOrderState ({ commit }) {
    commit('setIsTimeout', false)
    commit('setIsError', false)
    commit('setErrorMsg', '')
  },
  pollingOrderStatus ({ dispatch, commit }) {
    // start polling
    commit('setPolling', true)
    dispatch('getOrderDetail')
  },
  getOrderDetail ({ dispatch, state, commit }) {
    dispatch('resetOrderState')
    api
      .request(
        'hunan/passimeter/Consumer/getOrderDetail',
        { 'order_id': state.orderId }
      ).then(res => {
        if (res && res.data && (res.data.timeout || res.data.ticket_status !== '0')) {
          // stop and clear timer until timeout or ticket status is 1 (success) or 2 (error)
          dispatch('stopPolling')
          if (res.data.timeout) {
            commit('setIsTimeout', true)
          }
        }
        // if has timer start polling
        if (state.polling) {
          _timer = setTimeout(() => {
            dispatch('getOrderDetail', _timer)
          }, 2000)
        } else {
          commit('setOrderDetail', res.data)
        }
      }).catch(error => {
        dispatch('stopPolling')
        commit('setIsError', true)
        commit('setErrorMsg', error && error.msg ? error.msg : ', ')
      })
  },
  stopPolling ({ commit }) {
    if (_timer) {
      clearTimeout(_timer)
      _timer = null
    }
    commit('setPolling', false)
  }
}

const mutations = {
  resetOrderState (state) {
    state.isTimeout = false
  },
  increase (state) {
    state.num++
  },
  decrease (state) {
    state.num--
  },
  setOrderId (state, orderId) {
    state.orderId = orderId
  },
  setOrderDetail (state, data) {
    state.orderDetail = data
  },
  setPolling (state, bool) {
    state.polling = bool
  },
  setNum (state, num) {
    state.num = num
  },
  setIsTimeout (state, bool) {
    state.isTimeout = bool
  },
  setIsError (state, bool) {
    state.isError = bool
  },
  setErrorMsg (state, msg) {
    state.errorMsg = msg
  },
  setSubmitting (state, bool) {
    state.submitting = bool
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



