import { api } from '../../api'

const state = {
  machineId: '',
  shopName: '',
  shopAddress: '',
  lottery: '',
  lotteryImg: '',
  lotteryId: '',
  stocks: 0,
  price: 0,
  msgCode: '',
  lockedMsgMap: {},
  isLoading: true,
  isError: false,
  errorMsg: ''
}

const getters = {
  price: state => state.price,
  lockedMsg: state => state.lockedMsgMap[state.msgCode] || ''
}

const actions = {
  getMachineInfo ({ commit }) {
    commit('setIsLoading', true)
    commit('setIsError', false)
    api
      .request('hunan/passimeter/Consumer/getmachineInfo')
      .then(data => {
        if (data.data) {
          commit('setMachineInfo', data.data)
        } else {
          commit('setIsError', true)
          commit('setErrorMsg', ', ')
        }
      }).catch(error => {
        commit('setIsError', true)
        commit('setErrorMsg', error && error.msg ? error.msg : ', ')
      }).then(() => {
        commit('setIsLoading', false)
      })
  },
  setMsgCode ({ commit }) {
    let msgCode = api.getUrlParamsBy('msg') || ''
    commit('setMsgCode', msgCode)
  },
  getLockedMsgMap ({ commit }) {
    api
      .get('hunan/passimeter/Consumer/resJson')
      .then(data => {
        if (data.data) {
          commit('setLockedMsgMap', data.data)
        }
      })
  }
}

const mutations = {
  setMachineInfo (state, data) {
    state.machineId = data.machine_id || ''
    state.shopName = data.viewname || ''
    state.shopAddress = data.address || ''
    state.lottery = data.ticket_name || ''
    state.lotteryImg = data.image || ''
    state.lotteryId = data.ticket_id || ''
    state.stocks = Number(data.ticket_num) || 0
    state.price = Number(data.price) || 0
  },
  setMsgCode (state, code) {
    state.msgCode = code
  },
  setLockedMsgMap (state, data) {
    state.lockedMsgMap = data
  },
  setIsLoading (state, is) {
    state.isLoading = is
  },
  setIsError (state, is) {
    state.isError = is
  },
  setErrorMsg (state, msg) {
    state.errorMsg = msg
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



