<template>
  <div class="container pt-100">
    <div v-for = "item in data" :key="item.index" class="mb-20">
      <list-select :info="item.info" :list="item.list" :init="item.initData" @selectChange="selectChange($event,item)"></list-select>
    </div>
    <div>
      <div class="remain"><span></span><span class="i-red">{{initResData.ticket_num}}</span> </div>
    </div>
    <div class="add-number-box">
      <div class="title"></div>
      <div class="input-box">
        <div class="input-btn" @click="minus">-</div>
        <input class="input-number ml-5" type="text" v-model.number="amount">
        <div class="input-btn ml-5"  @click="add" >+</div>
      </div>
    </div>
    <div class="mt-60 btn-box">
      <button class="btn" @click="addLottery"></button>
    </div>
  </div>
</template>
<script>
import listSelect from '@/shared/components/list-select'
import {api} from '@/api'
import { MessageBox } from 'mint-ui'
export default {
  components: {
    listSelect
  },
  data () {
    return {
      data: [
        {
          index: 0,
          info: {
            lab: '',
            title: '',
            id: 'lotteryId',
            icon: 'icon-money i-green f-18'
          },
          initData: null,
          list: [
            {
              id: 'null',
              value: ''
            }
          ],
          selectItem: null
        },
        {
          index: 1,
          info: {
            lab: '',
            title: '',
            id: 'lotteryName',
            icon: 'icon-lottery i-green'
          },
          initData: null,
          list: [
            {
              id: 'null',
              value: ''
            }
          ],
          selectItem: null
        }
      ],
      lotteryId: '',
      machine_id: '',
      amount: 60,
      lotteryName: '',
      num: 10,
      initResData: {
        price: '',
        ticket_id: '',
        ticket_num: '0'
      },
      submiting: false
    }
  },
  created () {
    this.machine_id = this.$route.query.machine_id || ''
    this.getInitData().then(res => {
      if (res) {
        this.initResData = res
      }
      this.getlotteryName(this.initResData.price)
      this.getlotteryList(this.initResData.price, this.initResData.ticket_id)
    })
  },
  methods: {
    selectChange (obj, item) {
      let e = obj.data
      if (item.info.id === 'lotteryId') {
        let code = e[e.length - 1].id
        if (!obj.isInit) {
          this.getlotteryList(code)
        }
      }
      if (item.info.id === 'lotteryName') {
        console.log(e)
        this.lotteryId = e[0].id
        this.amount = e[0].num
        this.num = e[0].num
        this.lotteryName = e[0].title
      }
    },
    async getInitData () {
      let initData = await api.requestWithSid('hunan/passimeter/Machine/getLastTicketId')
        .then(res => {
          if (res.code === 0) {
            return res.data
          } else {
            return null
          }
        })
        .catch(err => console.log(err))
      return initData
    },
    getlotteryName (price) {
      api.requestWithSid('hunan/passimeter/Machine/getTicketType')
        .then(res => {
          if (res.data.length) {
            this.data[0].list = res.data
            if (price) {
              this.data[0].initData = {
                id: price
              }
            } else {
              this.data[0].initData = null
            }
          } else {
            this.data[0].list = [
              {
                id: 'null',
                value: ''
              }
            ]
          }
        })
        .catch(err => {
          MessageBox.alert(err.msg)
          console.log(err)
        })
    },
    getlotteryList (code, id) {
      if (!code) {
        return
      }
      api.requestWithSid('hunan/passimeter/Machine/getTicketList', {price: code})
        .then(res => {
          if (res.data.length) {
            this.data[1].list = []
            this.lotteryId = 'null'
            for (let item of res.data) {
              item['value'] = item.title
            }
            this.data[1].list = res.data
            if (id) {
              this.data[1].initData = {
                id: id
              }
            } else {
              this.data[1].initData = null
            }
          } else {
            this.data[1].list = [
              {
                id: 'null',
                value: ''
              }
            ]
          }
        })
        .catch(err => {
          MessageBox.alert(err.msg)
          console.log(err)
        })
    },
    minus () {
      this.amount -= this.num
      if (this.amount < 0) {
        this.amount = 0
      }
    },
    add () {
      this.amount += this.num
    },
    addLottery () {
      if (this.submiting) {
        return
      }
      if (this.lotteryId && this.lotteryId !== 'null') {
        let reaData = {
          ticket_num: this.amount,
          machine_id: this.machine_id,
          ticket_id: this.lotteryId
        }
        if (reaData.ticket_id !== this.initResData.ticket_id && Number(this.initResData.ticket_num) > 0) {
          MessageBox.confirm('')
            .then(action => {
              this.addLotteryByApi(reaData)
            }, cancel => {
            })
        } else {
          this.addLotteryByApi(reaData)
        }
      } else {
        MessageBox.alert('')
      }
    },
    addLotteryByApi (reaData) {
      MessageBox.confirm(`<div>&nbsp; ${this.lotteryName}*${reaData.ticket_num} &nbsp;</div><div></div>`)
        .then(ok => {
          this.submiting = true
          api.requestWithSid('hunan/passimeter/Machine/addTicket', reaData)
            .then(res => {
              this.initResData.ticket_num = res.data
              MessageBox.alert('')
            }).then(() => {
              this.$router.push({path: '/add-success', query: {num: this.initResData.ticket_num}})
            })
            .catch(err => {
              console.log(err)
              MessageBox.alert(err.msg)
            })
            .then(() => {
              this.submiting = false
            })
        }, no => {})
    }
  }
}
</script>
<style scoped>
  .container{
    height: 100%;
    box-sizing: border-box;
    background: url('../../../assets/login-bg.png') no-repeat;
    background-size: 100% 100%;
    background-attachment: fixed;
    color: #434345;
  }
  .mt-60{
    margin-top: 60px !important;
  }
  .mb-20{
    margin-bottom: 35px;
  }
  .pt-100{
    padding-top: 100px;
  }
  .add-number-box{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 80%;
    margin: auto;
    margin-top: 20px;
  }
  .add-number-box .title{
    color: #035F41;
    font-weight: 550;
  }
  .input-box{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .input-box .input-btn{
    width: 50px;
    height: 60px;
    background: #fff;
    font-size: 20px;
    text-align: center;
    line-height: 60px;
  }
  .ml-5{
    margin-left: 5px;
  }
  .input-box .input-number{
    color: rgba(0,0,0,0.5);
    height: 60px;
    font-size: 20px;
    width: 70px;
    border: 0 !important;
    padding: 0 !important;
    outline: none;
    text-align: center;
    border-radius: 0 !important;
  }
  .btn-box{
    width: 80%;
    margin: auto;
  }
  .btn {
    width: 100%;
    margin: auto;
    height: 55px;
    background: #006d55;
    color: #fff;
    border: 0;
    outline: none;
    font-size: 18px;
    -moz-box-shadow:2px 2px 15px #006d55;
    -webkit-box-shadow:2px 2px 15px #006d55;
    box-shadow:2px 2px 15px #006d55;
  }
  .btn:active {
    background: #005b48 ;
  }
  .remain{
    width: 80%;
    margin: auto;
    text-align: right;
    color: #666;
    font-size: 14px;
  }
</style>



