import Vue from 'vue'
import Vuex from 'vuex'
import session from './modules/session'
import machineInfo from './modules/machine-info'
import orders from './modules/orders'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    session,
    machineInfo,
    orders
  }
})



