<template>
  <div class="min-h">
    <div class="loading text-center" v-if="machine.isLoading">
      <mt-spinner type="triple-bounce" color="#035F41"></mt-spinner>
    </div>
    <div class="notice-container" v-else-if="machine.isError">
      <img src="../../../assets/machine-lock.png">
      <div class="f-16">{{machine.errorMsg}}</div>
      <div class="btn-container">
        <a class="btn custom-btn-outline" @click="getDetail"></a>
      </div>
    </div>
    <div class="pay-lottery" v-else>
      <div class="store">
        <img src="../../../assets/shop.png" alt="">
        <div class="store-info">
          <div class="f-16">{{machine.shopName}}</div>
          <div class="store-add f-14">{{machine.shopAddress}}</div>
        </div>
      </div>
      <div class="lottery-img">
        <img :src="machine.lotteryImg" alt="">
      </div>
      <div class="lottery-info">
        <div class="info-list">
          <div class="info-list-item">
            <span class="left iconfont icon-value"></span>
            <span class="center">{{machine.lottery}}</span>
            <span class="right">{{machine.price}}</span>
          </div>
          <div class="info-list-item">
            <span class="left iconfont icon-ticket"></span>
            <span class="center"></span>
            <span class="right">
              <span class="red">{{machine.stocks}}</span>
            </span>
          </div>
        </div>
        <div class="add-lottery">
          <div class="select-ten" @click="setToTen">10</div>
          <div class="add">
            <i @click="$store.dispatch('decrease')"><svg width="32px" height="32px" viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg"><path fill="#b8b8b8" d="M512 1024C229.180952 1024 0 794.819048 0 512S229.180952 0 512 0 1024 229.180952 1024 512 794.819048 1024 512 1024z m0-959.975619a448.024381 448.024381 0 1 0 0 896 448.024381 448.024381 0 0 0 0-896zM243.809524 534.186667A46.811429 46.811429 0 0 1 290.474667 487.619048h464.993523c25.795048 0 46.665143 20.675048 46.665143 46.567619a46.811429 46.811429 0 0 1-46.665143 46.567619H290.474667A46.470095 46.470095 0 0 1 243.809524 534.186667z"  /></svg></i>
            <span>{{num}}</span>
            <i @click="increase"><svg width="32px" height="32px" viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg"><path fill="#016e54" d="M512 1024C229.238154 1024 0 794.761846 0 512S229.238154 0 512 0s512 229.238154 512 512-229.238154 512-512 512z m236.307692-551.384615H551.384615V275.692308a39.384615 39.384615 0 1 0-78.76923 0v196.923077H275.692308a39.384615 39.384615 0 1 0 0 78.76923h196.923077v196.923077a39.384615 39.384615 0 1 0 78.76923 0V551.384615h196.923077a39.384615 39.384615 0 0 0 0-78.76923z"/></svg></i>
          </div>
        </div>
      </div>
      <div class="pay">
        <div class="total text-left f-14">
          <span></span>
          <span class="f-20 red">{{total}}</span>
        </div>
        <div class="go-pay f-18" :class="{'disabled': submitting}" @click="$store.dispatch('submitOrder')"></div>
      </div>
      <div class="notice-info f-14">18</div>
    </div>
  </div>
</template>

<script>
import { MessageBox } from 'mint-ui'
export default {
  name: 'Orders',
  created () {
    this.$store.dispatch('resetOrder')
    this.getDetail()
  },
  methods: {
    increase () {
      if (this.num >= this.$store.state.machineInfo.stocks) {
        this.openAlert()
      } else {
        this.$store.dispatch('increase')
      }
    },
    openAlert (msg) {
      MessageBox({
        message: msg || ''
      })
    },
    getDetail () {
      this.$store.dispatch('getMachineInfo')
    },
    setToTen () {
      if (this.$store.state.machineInfo.stocks < 10) {
        this.openAlert('10')
      } else {
        this.$store.dispatch('setNumToTen')
      }
    }
  },
  computed: {
    machine () {
      return this.$store.state.machineInfo
    },
    total () {
      return this.$store.getters.totalPrice
    },
    num () {
      return this.$store.state.orders.num
    },
    submitting () {
      return this.$store.state.orders.submitting
    }
  }
}
</script>

<style scoped>
* {
  box-sizing: border-box;
}
.min-h {
  min-height: 100%;
  background-color: #f0f3f5;
}
.notice-container {
  text-align: center;
  letter-spacing: 1px;
  color: rgba(0, 0, 0, 0.7);
}
.notice-container img {
  margin-top: 20%;
  margin-bottom: 20px;
}
.loading {
  padding-top: 60%;
}
.red {
  color: #f00;
}
.pay-lottery {
  color: #676767;
}
/* -------------------------- */
/* store info */
.store {
  padding: 15px;
  background-color: #fff;
  display: flex;
  align-items: center;
}
.store > img {
  height: 35px;
  width: 35px;
}
.store .store-info {
  margin-left: 10px;
}
.store .store-info .store-add {
  margin-top: 5px;
}
/* -------------------------- */
/* lottery img */
.lottery-img {
  padding: 12px;
}
.lottery-img img {
  width: 100%;
}
/* ---------------------------- */
/* lottery detail */
.lottery-info {
  background: #fff;
}
.info-list {
  padding: 10px 0;
  margin: 0 10px;
  border-bottom: 1px solid #ddd;
}
.info-list-item {
  padding: 5px;
  font-size: 14px;
  display: flex;
  align-items: center;
}
.info-list-item .iconfont {
  font-size: 20px;
  color: #006d55;
}
.info-list-item .left {
  padding-right: 10px;
  order: 0;
}
.info-list-item .center {
  order: 1;
  margin-right: auto;
  padding-right: 10px;
}
.info-list-item .right {
  order: 2;
}
/* ticket number opreation area*/
.add-lottery {
  padding: 20px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.add-lottery .select-ten {
  background: #006d55;
  color: #fff;
  padding: 6px 10px;
  margin-right: 15px;
  font-size: 12px;
  letter-spacing: 2px;
}
.add-lottery .add {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.add-lottery .add i {
  height: 32px;
}
.add-lottery .add span {
  width: 100px;
  text-align: center;
  margin: 0 8px;
  padding: 5px 10px;
  border: 1px solid #ddd;
}
/* -------------------------------- */
/* pay area */
.pay {
  background: #fff;
  display: flex;
  justify-content: space-between;
  margin: 12px 0;
}
.pay > div {
  width: 50%;
  padding: 12px;
}
.pay .total {
  color: #969998;
  align-items: center;
  display: flex;
}
.total > span:first-child {
  margin-right: 5px;
}
.pay .go-pay {
  text-align: center;
  background-color: #016e54;
  color: #fff;
}
.go-pay.disabled {
  opacity: 0.8;
}
/* ------------------------------- */
.notice-info {
  text-align: center;
  color: rgba(0, 0, 0, 0.6);
  padding-bottom: 10px;
}
.btn-container {
  margin: 10%;
}
</style>



