<template>
  <div>
    <div v-if="apiState.isError" class="notice-container text-center">
      <img src="../../../assets/machine-lock.png">
      <div class="f-16 text-gray my">{{apiState.errorMsg}}</div>
      <div class="btn-container">
        <a class="btn custom-btn-outline" @click="getDetail"></a>
      </div>
    </div>
    <div v-else-if="apiState.polling" class="notice-container text-center">
      <img src="../../../assets/waiting.png">
      <div class="f-16 text-gray my">, </div>
      <mt-spinner type="triple-bounce" color="#035F41"></mt-spinner>
    </div>
    <div v-else>
      <div v-if="isSuccess">
        <div class="text-center banner success">
          <div class="f-16"></div>
          <div class="f-14">, </div>
          <img src="../../../assets/success-signet.png" class="signet">
        </div>
      </div>
      <div v-else>
        <div class="text-center banner fail">
          <div v-if="isTimeout">
            <div class="f-16"></div>
            <div class="f-14" >, </div>
          </div>
          <div v-else>
            <div class="f-16"></div>
            <div class="f-14">, </div>
          </div>
          <img src="../../../assets/fail-signet.png" class="signet">
        </div>
        <div class="item">
          <span class="iconfont icon-customer-service text-light-primary left"></span>
          <span class="text-gray pr"></span>
          <span class="text-light-primary">15088655716</span>
        </div>
      </div>
      <div class="detail-list f-14" :class="{success: isSuccess}">
        <div class="item divider">
          <span class="left iconfont icon-shop"></span>
          <span class="center text-gray">{{machine.shopName}}</span>
        </div>
        <div class="item divider">
          <span class="left iconfont icon-lottery-bill"></span>
          <span class="center">{{machine.lottery}}</span>
          <span class="right">x {{order.ticket_num}}</span>
        </div>
        <div class="item" :class="{divider: !isSuccess}">
          <span class="left iconfont icon-total"></span>
          <span class="center text-gray"></span>
          <span class="right text-gray">{{order.total_fee}}</span>
        </div>
        <div class="item" v-if="!isSuccess">
          <span class="left iconfont icon-ticket-success"></span>
          <span class="center text-gray"></span>
          <span class="right text-gray">{{order.real_ticket_num}}</span>
        </div>
      </div>
      <div class="btn-container">
        <a class="btn custom-btn btn-block shadow" v-if="isSuccess" @click="toOrder"></a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PaySuccess',
  created () {
    this.$store.dispatch('getOrderId')
    this.getDetail()
  },
  computed: {
    order () {
      return this.$store.state.orders.orderDetail
    },
    polling () {
      return this.$store.state.orders.polling
    },
    machine () {
      return this.$store.state.machineInfo
    },
    isSuccess () {
      return this.$store.getters.isSuccess
    },
    isTimeout () {
      return this.$store.state.orders.isTimeout
    },
    apiState () {
      return this.$store.getters.apiState
    }
  },
  methods: {
    toOrder () {
      this.$router.push('/orders')
    },
    getDetail () {
      this.$store.dispatch('getMachineInfo')
      this.$store.dispatch('pollingOrderStatus')
    }
  }
}
</script>

<style scoped>
.notice-container {
  padding-top: 40%;
}
.text-light-primary {
  color: #49b995;
}
.pr {
  padding-right: 10px;
}
.my {
  margin: 5% 0;
}
.success.banner {
  background: url(../../../assets/order-success.png) no-repeat;
  background-size: 100%;
}
.fail.banner {
  background: url(../../../assets/order-fail.png) no-repeat;
  background-size: 100%;
}
.banner {
  padding: 30px 15px;
  color: rgba(255, 255, 255, 0.9);
  letter-spacing: 1px;
  position: relative;
  background-color: #f0f3f5;
}
.banner > .signet {
  position: absolute;
  top: 20px;
  right: 0;
}
.banner > div:first-child {
  margin-bottom: 5px;
}
.detail-list {
  border-top: 10px solid #f0f3f5;
}
.item {
  padding: 10px;
  background: #fff;
  font-size: 14px;
  display: flex;
  align-items: center;
}
.item.divider {
  border-bottom: 1px solid #eee;
}
.item .iconfont {
  font-size: 20px;
  color: #49b995;
}
.success .item .iconfont {
  color: #006d55;
}
.item .left {
  padding-right: 10px;
  order: 0;
}
.item .center {
  order: 1;
  margin-right: auto;
  padding-right: 10px;
}
.item .right {
  order: 2;
}
.btn-container {
  margin: 10%;
}
</style>



