<template>
  <div class="page-content">
    <div v-if="isError" class="notice-container">
      <img src="../../../assets/machine-lock.png">
      <div class="f-16 text-gray my">{{errorMsg}}</div>
    </div>
    <div v-else class="detail text-center">
      <div class="top">
        <span class="iconfont icon-fail-circle f-80 text-primary"></span>
        <div class="f-16"></div>
      </div>
      <div class="bottom">
        <div class="item">
          <span class="left iconfont icon-shop"></span>
          <span class="center text-gray">{{machine.shopName}}</span>
        </div>
        <div class="item">
          <span class="left iconfont icon-lottery-bill"></span>
          <span class="center">{{machine.lottery}}</span>
          <span class="right">x {{order.ticket_num}}</span>
        </div>
        <div class="item">
          <span class="left iconfont icon-total"></span>
          <span class="center text-gray"></span>
          <span class="right text-gray">{{order.total_fee}}</span>
        </div>
      </div>
    </div>
    <div class="btn-container">
      <a class="btn custom-btn btn-block shadow" :class="isError ? 'custom-btn-outline':'custom-btn btn-block shadow'" @click="reorder"></a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PayFailure',
  created () {
    this.$store.dispatch('getMachineInfo')
    this.$store.dispatch('getOrderId')
    this.$store.dispatch('getOrderDetail')
  },
  computed: {
    order () {
      return this.$store.state.orders.orderDetail
    },
    machine () {
      return this.$store.state.machineInfo
    },
    isError () {
      return this.$store.state.orders.isError
    },
    errorMsg () {
      return this.$store.state.orders.errorMsg
    }
  },
  methods: {
    reorder () {
      // recover ticket number
      this.$store.dispatch('setNum', this.$store.state.orders.orderDetail.ticket_num)
      this.$router.push('orders')
    }
  }
}
</script>

<style scoped>
.f-80 {
  font-size: 80px;
}
.page-content {
  background: #f0f3f5;
  height: 100%;
  padding-top: 10px;
}
.detail {
  border-radius: 5px;
  background: #fff;
  margin: 10px;
  margin-top: 0;
}
.detail > div {
  padding: 0 10px;
}
.detail .top {
  border-bottom: 1px dashed #ddd;
  padding: 20px 10px;
}
.detail .bottom {
  padding: 5px 10px 20px;
}
.item {
  padding: 5px 0;
  background: #fff;
  font-size: 14px;
  display: flex;
  align-items: center;
}
.item .iconfont {
  font-size: 20px;
  color: #006d55;
}
.item .left {
  padding-right: 10px;
  order: 0;
}
.item .center {
  order: 1;
  margin-right: auto;
  padding-right: 10px;
}
.item .right {
  order: 2;
}
.btn-container {
  margin: 10%;
}
</style>



